nav
    position: fixed
    overflow: hidden
    background: linear-gradient(0deg, rgba(37,63,133,1) 0%, rgba(65,96,230,1) 100%)
    height: 50px
    top: 0
    width: 100%
    ul
        list-style: none
        img
            height: 40px
            width: 40px
        li
            display: inline-block
            float: left
            color: white
            font-size: 25px
            p
                text-decoration: none
                font-size: 28px
                color: white
                margin-left: 10rem
            p:hover
                font-size: 28px
                color: black
                cursor: pointer

